body {
    font-size: 15px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

#main {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    flex-direction: column;
}

#map-container {
    display: flex;
    flex-direction: column;
    flex-grow: 0;
    height: 55vh;
    position: relative;
}

#directions-container {
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    flex-grow: 1;
    flex-basis: 0;
    position: relative;
    z-index: 1;
    overflow: hidden;
    background-color: white;
    box-shadow: 0 -2px 4px 0 rgba(0, 0, 0, 0.12);
}

#map {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

@media (min-width: 630px) {
    #main {
        flex-direction: row;
        height: 100%;
    }

    #map-container {
        flex-basis: 73vw;
        order: 2;
        position: relative;
        height: 100%;
    }

    #directions-container {
        flex-basis: 27vw;
        min-width: 300px !important;
        max-width: 480px !important;
        order: 1;
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
        -webkit-box-flex: 0;
        flex-grow: 0;
        height: 100%;
    }
}

#directions-results {
    display: flex;
    flex-direction: column;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #fff;
    z-index: 2;
    overflow-y: auto;
}


#directions-results ul {
    margin: 0 !important;
    padding: 0;
    list-style: none;
}

#directions-results button {
    border: none;
}

#directions-inputs {
    padding: 1.3rem;
    background-color: #008248;
    position: relative;
}

#directions-options {
    padding: .8rem 1.3rem;
    color: rgba(255, 255, 255, 0.87);
    background: #036235;
    position: relative;
}

#od-inputs {
    position: relative;
    max-width: 98%;
}

input {
    padding: .5rem .6rem;
    border-radius: .25rem;
    font-size: 1.125rem;
    text-overflow: ellipsis;
    border-color: transparent;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: #fff;
    color: inherit;
    display: block;
    font-family: inherit;
    position: relative;
    width: 100%;
    line-height: inherit;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, .3);
    margin-top: .7rem;
    box-sizing: border-box;
}

input[type="text"]:focus {
    outline: 0;
    box-shadow: 0 0 0 3px rgba(3, 98, 53, 0.85);

}


.flex-grow {
    flex-grow: 1;
}

button {
    display: block;
    cursor: pointer;
    background: transparent;
    border: 0;
    border-radius: 0;
    font: inherit;
    list-style: none;
    margin: 0;
    outline: 0;
    overflow: visible;
    padding: 0;
    vertical-align: baseline;
    position: relative;
}

.reverse-icon {
    right: -1.3rem;
    width: 20px;
    height: 20px;
    background-size: cover;
    background-image: url("https://images.woosmap.com/directions/revert_od.png")
}

.reverse-icon:hover {
    background-image: url("https://images.woosmap.com/directions/revert_od_selected.png")
}

.markerLabel {
    font-weight: 600;
    color: rgba(0, 0, 0, 0.75) !important;
    text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white;
}
