.directions-travel-mode-selector{
    display: flex;
    align-content: flex-start;
    justify-content: space-between;
    max-width: 200px;
    text-align: center;
    height: 48px;
}

.directions-travel-mode-icon {
    width: 24px;
    height: 24px;
    margin: 12px 9px 11px 9px;
}

.drive-icon, .walk-icon, .bicycle-icon, .transit-icon {
    display: block;
}

.selected .drive-icon, .selected .walk-icon, .selected .bicycle-icon, .selected .transit-icon {
    display: none;
}

.drive-icon-selected, .walk-icon-selected, .bicycle-icon-selected, .transit-icon-selected {
    display: none;
}

.selected .drive-icon-selected, .selected .walk-icon-selected, .selected .bicycle-icon-selected, .selected .transit-icon-selected {
    display: block;
}

.directions-travel-mode-selector .selected {
    z-index: 0;
}

.directions-travel-mode-selector .selected::after, .directions-travel-mode-selector .selected:hover::after {
    content: '';
    border-radius: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: -1;
    margin: 6px 3px 5px 3px;
    height: 36px;
    width: 36px;
    background-color: rgba(0,0,0,.25);
}

.directions-origin, .directions-destination {
    position: relative;
}

.directions-icon {
    cursor: pointer;
    position: absolute;
    color: white;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    z-index: 1;
}

