.section-directions-trip {
    position: relative;
    padding: 1.3rem;
    font-size: 14px;
    cursor: pointer;
    border-bottom: 1px solid #e6e6e6;
    color: rgba(0, 0, 0, 0.54);
    flex: none;
}

.section-directions-trip.selected:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    border-left: 5px solid #87d7a8;
    height: 100%;
}

.section-directions-trip-details-msg {
    display: none;
}

.section-directions-trip.selected .section-directions-trip-details-msg {
    display: block;
}

.section-directions-trip-travel-mode-icon {
    position: absolute;
    top: 18px;
    width: 24px;
    height: 24px;
    opacity: 0.6;
}

.section-directions-trip-description {
    padding-left: 48px;
}

.section-directions-trip-numbers {
    float: right;
    text-align: right;
    padding-left: 10px;
}

.section-directions-trip-duration, .section-directions-trip-title {
    color: rgba(0, 0, 0, 0.87);
    font-size: 15px;
    vertical-align: top;
}

.section-directions-trip-duration {
    color: #188038;
}

.section-directions-trip-distance, .section-directions-trip-summary {
    line-height: 16px;
    padding: 4px 0;
    font-size: 13px;
}

.section-directions-trip-details-msg {
    font-size: 13px;
    font-weight: 500;
    text-transform: uppercase;
    color: #1a73e8;
}

#routes.loading {
    background: url(https://demo.woosmap.com/storelocator/images/loader.svg) no-repeat;
    background-position: 50% center;
    height: 100px;
}

#routes.loading .section-directions-trip {
    display: none;
}

.directions-instructions {
    display: flex;
    flex-direction: column;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #fff;
    z-index: 2;
    overflow-y: auto;
    position: absolute;
}

.directions-trip-header {
    flex: none;
    display: flex;
    background: #008248;
    padding: 1.3rem;
    box-sizing: border-box;
}

.directions-trip-header-back {
    background: no-repeat url(https://images.woosmap.com/directions/back.png);
    background-size: 24px 24px;
    flex: 0 0 24px;
    cursor: pointer;
    margin: 0 20px 0 -4px;
}

.directions-trip-header-fromto {
    flex: 1 1 auto;
    overflow: hidden;
}

.directions-trip-header-to, .directions-trip-header-from {
    color: rgba(255, 255, 255, 0.87);
}

.directions-trip-header-waypoint {
    color: rgba(255, 255, 255, 0.45);
    font-size: 13px;
    line-height: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.directions-trip-summary-header {
    font-size: 20px;
    flex: 1 1 auto;
    color: #008248;
    padding: 1.3rem;
    border-bottom: 1px solid #e6e6e6;
}

.directions-trip-summary-subtitle {
    color: rgba(0, 0, 0, 0.54);
    font-weight: 300;
}

.directions-trip-summary-description {
    display: block;
    color: rgba(0, 0, 0, 0.54);
    font-size: 13px;
    padding-top: 8px;
}

.directions-waypoint {
    padding: 1.3rem 1.3rem 0;
}

.directions-waypoint h4 {
    margin: 0;
}

.directions-trip-actions {
    display: flex;
    align-items: baseline;
    float: right;
    margin: 1.2rem 1.3rem 0 0;
}

.directions-trip-actions-button {
    height: 24px;
    width: 24px;
    background: no-repeat url(https://images.woosmap.com/directions/share.png);
    background-size: 24px 24px;
    opacity: 0.54;
    margin-left: 12px;
    cursor: pointer;
}

.directions-trip-actions-button:hover {
    opacity: 0.87;
}