/*------------override google maps style----------*/
.adp {
    color: rgba(0, 0, 0, 0.87);
    font-family: inherit !important;
    padding: 0 1.3rem 1.3rem;
    flex: 1 1 auto;
}

.adp * {
    font-family: inherit !important;
}

.adp-placemark {
    display: none;
}

.adp-directions tbody tr td:nth-child(3n-1) {
    display: none;
}

.adp-directions tbody tr td:nth-child(5n-1) {
    padding-right: 0 !important;
}

.adp-substep {
    border-top: none !important;
    border-bottom: 1px solid #e6e6e6;
    position: relative;
    padding: .8rem !important;
}

.adp-distance {
    display: inline-block;
    font-size: 11px;
    color: #777;
    font-weight: 300;
    background-color: #fff;
    padding-right: 5px;
    z-index: 1;
}

.adp-legal, .adp-summary {
    display: none;
}